.titleAndButtonBox {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 30px 0;

  .title {
    font-weight: 600;
    font-size: 22px;
    line-height: 27px;
  }
}

.searchTitle {
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 10px;
}

.isEmpty {
  width: 100%;
  text-align: center;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #000000;
  opacity: 0.5;
  margin-top: 100px;
  margin-bottom: 40px;
}

.isEmptyWrap {
  display: flex;
  justify-content: center;
}

.customButton {

}

.notFound {
  text-align: center;
  margin: 100px 0;
  color: rgba(0, 0, 0, 0.7);
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
}

.disabled {
  pointer-events: none;
  opacity: 0.5;
}