.default {
  background-color: #366EFF;
  padding: 8px 28px;
  font-family: Montserrat;
  font-size: 16px;
  font-weight: 500;
  line-height: 20px;
  letter-spacing: 0.01em;
  border: none;
  color: #FFFFFF;
  border-radius: 30px;
  transition: all .3s ease-in-out;
  cursor: pointer;
  box-shadow: 0 4px 18px rgba(54, 110, 255, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);

  &:hover {
    background-color: #295cde;
    box-shadow: 4px 6px 20px rgba(54, 110, 255, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
  }

  &.disabled {
    opacity: 0.8;
    cursor: default;

    &:hover {
      background-color: #366EFF;
    }

  }

  &.big {
    width: 100%;
  }

  &.noRadius {
    font-weight: 400;
    font-size: 12px;
    line-height: 24px;
    border-radius: 2px;
    padding: 0 10px;
  }

  &.white {
    background: #FFFFFF;
    filter: drop-shadow(0px 2px 10px rgba(109, 109, 109, 0.25));
    box-shadow: 0 2px 10px rgba(109, 109, 109, 0.25), inset 0 1px 0 rgba(255, 255, 255, 0.3);
    color: rgba(0, 0, 0, 0.8);

    &.disabled {
      pointer-events: none;
      background-color: #333;
      color: #FFFFFF;
      opacity: 0.15;
    }

    &:hover {
      box-shadow: 2px 4px 14px rgba(30, 29, 29, 0.25), inset 0 1px 0 rgba(152, 150, 150, 0.3);
    }
  }

  &.red {
    background: #FF3636;
    box-shadow: 0 4px 18px rgba(255, 54, 54, 0.35), inset 0 1px 0 rgba(255, 255, 255, 0.3);
  }
}





