.loginForm {

  .input {
    margin-bottom: 15px;
    font-family: 'Montserrat';

    &:nth-child(2) {
      margin-bottom: 6px;
    }

    &:nth-child(3) {
      margin-bottom: 8px;
    }
  }

  .forgotText {
    text-align: right;
    margin-bottom: 60px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;


    &.isDisabled {
      pointer-events: none;
    }

    & > a {
      cursor: pointer;
      color: #366EFF;

      &:hover {
        border-bottom: 1px solid;
      }
    }


  }
}